.photo-list {
  padding: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 30px 0;
  padding: 0 50px;
  justify-content: center;
  cursor: pointer;
}

.photo-list li {
  list-style-type: none;
  margin: 5px;
}

.photo-list li img {
  width: 150px;
  height: 150px;
}