.authors-ul {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.authors-ul li {
  list-style-type: none;
}

.author-name {
  text-decoration: none;
  color:white;
  font-size: 20px;
  line-height: 40px;
  font-weight: 500;
}

.author-name:hover {
  color:grey;
}